import React from "react";
import "./HistoryPage.css";

function HistoryPage(props) {
  return (
    <div
      className="HistoryPage"
      style={{
        width: "80%",
        height: "100vh",
        margin: "auto",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        flexDirection: "column",
      }}
    >
      <div>
        <h1>Payment History</h1>
      </div>
      <br />

      <table>
        <thead>
          <tr>
            {" "}
            <th>Item Name</th>
            <th>Price</th>
            <th>Quantity</th>
            <th>Date of Purchase</th>
            <th>Payment Id</th>
          </tr>
        </thead>

        <tbody>
          {props.user.userData &&
            props.user.userData.history &&
            props.user.userData.history.map((item) => (
              <tr key={item.id}>
                {" "}
                <td>{item.name}</td> <td>{item.price}</td>
                <td>{item.quantity}</td>
                <td>{item.dateOfPurchase}</td> <td>{item.id}</td>
              </tr>
            ))}
        </tbody>
      </table>
    </div>
  );
}

export default HistoryPage;
